@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300&family=Rubik:wght@300&display=swap");

* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
  /* background: yellow; */
}

button {
  padding: 3px 10px;
  background: #1876d2;
  border: none;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}

button:hover {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px,
    rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
}

input[type="file"] {
  display: none;
}

.headerContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 20px;
  background-color: yellow;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px,
    rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

.footerContainer {
  position: absolute;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 20px;
  background-color: yellow;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px,
    rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}
.introContainer {
  /* background: yellow; */
  min-width: 100vh;
  min-height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid red;
  padding: 10px;
}

.introHeading {
  font-weight: bold;
}
.introdescription {
  font-weight: 500;
  line-height: 35px;
}

.getStartBtn {
  color: white;
  font-size: 20px;
  padding: 10px;
}

.prevScore {
  position: absolute;
  top: 50%;
  left: 10%;
  /* border: 2px solid green; */
  padding: 15px;
  border-radius: 10px;
  background-color: #1876d2;
  color: white;
}

.currentScore {
  position: absolute;
  top: 50%;
  right: 10%;
  padding: 15px;
  border-radius: 10px;
  background-color: #1876d2;
  color: white;
}
.navBtn {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

.profileContainer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 50px;
}

.scoreTable {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
}
.formEditContianer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  min-height: 80vh;
}

.profileImg {
  border: 2px solid #0d6dfd39;
  border-radius: 300px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px,
    rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

.mainContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 30px;
}

.registeringContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 25px;
  background-color: rgb(255, 255, 255);
}

.scoreContainer {
  min-height: 60vh;
}

.homeContainer {
  min-height: 80vh;
}

.headerProfileImg{
  margin-right: 15px;
}

/* OverWrited CSS from default*/

.css-cuefkz-MuiContainer-root {
  max-width: 1000px !important;
}

.css-1m6pqln {
  max-width: 1000px !important;
}

/* Responsive Style */

@media only screen and (max-width: 768px) {
  .registeringContainer {
    flex-direction: column;
    padding: 20px;
    padding-top: 0;
    overflow-x: hidden;
    gap: 20px;
  }

  .registerImage {
    text-align: center;
    width: 250px;
    height: 250px;
  }

  .mainContainer {
    flex-direction: column;
    padding-bottom: 30px;
  }

  .loginImage {
    text-align: center;
    width: 250px;
    height: 250px;
  }

  .homeContainerMain {
    padding: 20px;
    padding-top: 0;
  }
  .introImage {
    width: 200px;
    height: 200px;
  }

  .prevScore {
    position: absolute;
    top: 50%;
    left: 3%;
    /* border: 2px solid green; */
    padding: 15px;
    border-radius: 10px;
    background-color: #1876d2;
    color: white;
  }

  .prevScore h5,
  .currentScore h5 {
    font-size: 10px;
  }

  h5 {
    font-size: 15px;
  }

  .currentScore {
    position: absolute;
    top: 50%;
    right: 3%;
    padding: 15px;
    border-radius: 10px;
    background-color: #1876d2;
    color: white;
  }

  .profileContainer {
   flex-direction: column;
   padding: 20px;
   padding-top: 0;
  }


  .formEditContianer {
    flex-direction: column-reverse;
    padding-bottom: 20px;
  }

  

 .navBtn{
    /* position: absolute; */
    /* background: #FFFF00; */
    /* top: 0;
    right: 0;
    width: 50%; */
    min-height: 100px;
    border-radius: 10px;
    /* grid-template-columns: repeat(1,1fr); */
    display: flex;
    flex-direction: column;
    padding: 20px;
    transition: 0.7s ease;
    transform: translate(0);
  }

 
  .headerProfileImg{
    margin-left: 10px;
    margin-right: 15px;
  }
 

}
